#login{
    bottom: 0;
    z-index: 10;
    background: #f9f9f9;
    .toptitle{
        padding: 10px 0;
        text-align: center;
        background: #fff;
    }
    .content{
        width: 90%;
        margin: 50px auto 0;
        h3{
            font-weight: bolder;
            line-height: 40px;
        }
        .panel{
            min-height: 200px;
            padding: 50px 0 10px 0;
            border-radius: 20px;
            background: url("/img/tx.jpg") no-repeat;
            background-size: 100% 100%;
            p{
                position: relative;
                input{
                    height: 60px;
                    width: 100%;
                    padding-left:50px;
                    padding-right: 10px;
                    box-sizing: border-box;
                    display: block;
                    border: none;
                    background: rgba($color: #fff, $alpha: 0.6);
                    border-bottom: 1px solid #999;
                }
                input:nth-of-type(2){
                    padding-right: 60px;
                }
               .iconfont{
                  position: absolute;
                  left: 10px;
                  top: 15px;
                  font-size: 30px;
                  color: #999;
               }
               .sendcode{
                  position: absolute;
                  top: 15px;
                  right: 10px;
                  padding: 5px;
                  color: #fff;
                  border-radius: 5px;
                  background: #0ed30e;
              }
            }
            p:nth-of-type(3){
                padding: 10px 5px;
                text-align: right;
                font-size: 18px;
                color: #fff;
            }
            .btn{
                width: 95%;
                margin:20px auto;
                text-align: center;
                font-size: 20px;
                font-weight: bold;
                padding: 10px 0;
                color: #fff;
                border-radius: 10px;
                background: #0ed30e;
            }
        }
    }
    .thirdparty{
        .title{
            text-align: center;
            color: #ddd;
            padding: 20px 0;
            font-size: 18px;
        }
        .logo{
            display: flex;
            padding: 10px 0;
            justify-content: space-around;
            .iconfont{
                font-size: 50px;
            }
            .iconfont:nth-of-type(1){
                color: #3385ff;
            }
            .iconfont:nth-of-type(2){
                color: #0ed30e;
            }
            .iconfont:nth-of-type(3){
                color: #ff5b36;
            }
        }
        .protocol{
            text-align: center;
            padding: 10px 0;
            span{
                color: blue;
            }
        }
    }
}